/*Basic list*/

var React = require('react');
var PureRenderMixin = require('react/addons').addons.PureRenderMixin;
var Item=require('./multiLevelItem.jsx');
var _=require('lodash');
var CommonsMixins=require('./commonsMixins.jsx');
var KeywordSearch=require('./keywordSearch.jsx');
var SelectAllNone=require('./allNoneSelector.jsx');
var SelectionCounter = require('./selectionCounter.jsx');

module.exports = React.createClass({displayName: "exports",

  mixins: [CommonsMixins, PureRenderMixin],

  componentDidUpdate: function(){
    $(this.getDOMNode()).find('.filter-list-container').mCustomScrollbar({theme:"inset-dark"});    
  },

  getInitialState: function() {
    return {
      showOnlySelected: false,
      items: [],
      selected: [],
      filter: '',
    };
  },

  _getLists: function(lists, level) {
    lists.push(_.filter(this.state[level.levelParam], function(it){return !it.hide}));
    if (level.child){
      this._getLists(lists, level.child);
    }
  },

  render: function() {
    var lists = [];
    if (this.state.levels){
      this._getLists(lists, this.state.levels);
    } 
    if (this.props.active){
      return ( 
        React.createElement("div", {className: "tab-content"}, 
          React.createElement("div", {className: "tab-pane fade active in"}, 
            React.createElement("div", {className: "filter-group-panel selected"}, 
              
              React.createElement("div", {className: "filter-group-panel-header"}, 
                React.createElement("span", {className: "filter-label", role: "label"}, React.createElement(Message, {message: this.props.label}))
              ), 
              React.createElement(KeywordSearch, {onSearch: this._onSearch, lengthLimit: "2", onClear: this._onSearchClear}), 
              React.createElement("div", {className: "filter-list-container"}, 
                React.createElement("ul", {className: "filter-list"}, 
                
                  lists.map(function(list) {
                    return (list.map(function(item) {
                      return (React.createElement("li", null, React.createElement(Item, React.__spread({},  item, {onItemChange: this._onItemChange, showOnlySelected: this.state.showOnlySelected}))))
                    },this))                                       
                  },this) 
                
                )
              )
            )
          )
        )

        );
    } else {
      return null;
    }
  }

});